.Catalog-ObjectViewer {
  padding: 10px;
  background-color: #f2f2f2;
  border: 2px solid #09576d;
}

  .Catalog-ObjectViewer pre {
    outline: 1px solid #ccc;
    background-color: white;
    padding: 5px;
    margin: 5px;
  }

  .Catalog-ObjectViewer pre > span {
      white-space: normal;
      word-break: break-all;
    }

  .Catalog-ObjectViewer .string {
    color: green;

  }

  .Catalog-ObjectViewer .number {
    color: darkorange;
  }

  .Catalog-ObjectViewer .boolean {
    color: blue;
  }

  .Catalog-ObjectViewer .null {
    color: magenta;
  }

  .Catalog-ObjectViewer .key {
    color: red;
  }


